video {
	object-fit: cover;
	width: 100%;
	height: auto;
}

.main-container {
	.under-slider-info {
		margin: 20px 0 50px;
		display: inline-flex;
		justify-content: space-between;
		width: 100%;

		.bread-crumbs {
			position: relative;
			display: flex;
		}
	}

	.interiors-detail-info {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-column-gap: 32px;
		grid-template-rows: repeat(7, auto);
		grid-row-gap: 22px;
		font-style: normal;
		font-family: 'Inter',
			sans-serif;

		h1 {
			grid-column-start: 1;
			grid-column-end: 3;
			grid-row: 1;
			font-family: 'Manrope',
				sans-serif;
			font-weight: normal;
			font-size: 48px;
			line-height: 66px;
			color: #2E2E2E;
		}

		p {
			color: #808080;
			font-size: 14px;
			line-height: 24px;
		}

		p.interiors-detail-info__bold {
			font-weight: bold;
			font-size: 14px;
			line-height: 17px;
			color: #2E2E2E;
			margin-bottom: 5px;
		}

		&__status {
			grid-column: 1;
			grid-row: 2;
			display: flex;
			flex-direction: column;
		}

		&__all-area {
			grid-column: 2;
			grid-row: 2;
			display: flex;
			flex-direction: column;
		}

		&__type {
			grid-column: 1;
			grid-row: 3;
			display: flex;
			flex-direction: column;
		}

		&__dev {
			grid-column: 2;
			grid-row: 3;
			display: flex;
			flex-direction: column;
		}

		&__place {
			grid-column-start: 1;
			grid-column-end: 3;
			grid-row: 4;
		}

		&__autors {
			grid-column-start: 1;
			grid-column-end: 3;
			grid-row: 5;

			a {
				text-decoration: none;
				color: #808080;
				border-bottom: 5px solid transparent;
				margin-left: 7px;
			}

			a:first-child {
				margin-left: 0;
			}

			a:hover {
				color: #000;
				border-bottom: 1px solid #000;
			}
		}

		&__coautors {
			grid-column-start: 1;
			grid-column-end: 3;
			grid-row: 6;

			a {
				text-decoration: none;
				color: #808080;
				border-bottom: 5px solid transparent;
				margin-left: 7px;
			}

			a:first-child {
				margin-left: 0;
			}

			a:hover {
				color: #000;
				border-bottom: 1px solid #000;
			}
		}

		&__text {
			grid-column-start: 3;
			grid-column-end: 5;
			grid-row-start: 1;
			grid-row-end: 7;

			h2 {
				font-family: 'Manrope',
					sans-serif;
				font-weight: 600;
				font-size: 24px;
				line-height: 33px;
				margin-bottom: 50px;
			}
		}

		&__tags {
			width: 100%;
			height: 70px;
			grid-column-start: 1;
			grid-column-end: 5;
			grid-row: 7;
			display: inline-flex;
			justify-content: space-between;
			border-top: 1px solid #E4E4E4;

			&-buttons {
				display: inline-flex;
			}

			&-nav {
				display: inline-flex;
				margin: auto 0;
				cursor: pointer;

				&--text {
					position: relative;
					margin-right: 30px;
				}

				&--arrow {
					position: relative;
					top: 9px;
				}

				&--arrow.active {
					transform: rotate(.5turn);
				}
			}

			&--tag {
				background: #F5F5F5;
				border: 1px solid #DFDFDF;
				box-sizing: border-box;
				border-radius: 16px;
				text-align: center;
				margin: auto 0;
				padding: 10px;
				font-size: 10px;
				line-height: 12px;
				text-transform: uppercase;
				margin-left: 10px;
				cursor: pointer;
			}

			&--tag:first-child {
				margin-left: 0;
			}

			&--tag:hover {
				background: #B2B2B2;
			}
		}
	}
}

.interiors-detail-slabs {
	position: relative;

	&__status {
		display: flex;
		justify-content: space-between;
		height: 70px;
		margin: 0 100px;
		align-items: center;

		font-family: 'Inter', sans-serif;
		font-size: 14px;
		line-height: 17px;
		color: #808080;

		div:nth-child(2) {
			display: inline-flex;
		}
	}

	&__photos {
		display: grid;
		padding: 0 100px;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 32px;

		img {
			width: 100%;
		}
	}

	&__buttons {
		position: absolute;
		right: 40px;
		top: 70px;
		width: 20px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 150px;

		&--1-col {
			cursor: pointer;
			width: 20px;
			height: 10px;
			position: relative;
			display: flex;

			span {
				width: 20px;
				height: 10px;
				background: #B2B2B2;
				position: absolute;
				border-radius: 2px;
			}
		}

		&--2-col {
			cursor: pointer;
			width: 20px;
			height: 20px;
			position: relative;
			display: flex;

			span:nth-child(1) {
				width: 8px;
				height: 8px;
				position: absolute;
				top: 0;
				left: 0;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(2) {
				width: 8px;
				height: 8px;
				position: absolute;
				top: 0;
				right: 0;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(3) {
				width: 8px;
				height: 8px;
				position: absolute;
				bottom: 0;
				left: 0;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(4) {
				width: 8px;
				height: 8px;
				position: absolute;
				bottom: 0;
				right: 0;
				background: #B2B2B2;
				border-radius: 0.5px;
			}
		}

		&--3-col {
			cursor: pointer;
			width: 20px;
			height: 20px;
			display: flex;
			position: relative;

			span:nth-child(1) {
				width: 4px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 0;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(2) {
				width: 4px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 8px;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(3) {
				width: 4px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 16px;
				background: #B2B2B2;
				border-radius: 0.5px;
			}
		}

		&--4-col {
			cursor: pointer;
			width: 20px;
			height: 20px;
			display: flex;
			position: relative;

			span:nth-child(1) {
				width: 2px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 0;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(2) {
				width: 2px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 6px;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(3) {
				width: 2px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 12px;
				background: #B2B2B2;
				border-radius: 0.5px;
			}

			span:nth-child(4) {
				width: 2px;
				height: 20px;
				position: absolute;
				top: 0;
				left: 18px;
				background: #B2B2B2;
				border-radius: 0.5px;
			}
		}


	}

}

@media (max-width: 1400px) {}

@media (max-width: 1200px) {
	.main-container {
		.under-slider-info {
			margin: 15px 0 30px;
		}

		.interiors-detail-info {
			grid-template-columns: repeat(2, 1fr);
			grid-column-gap: 22px;
			grid-template-rows: repeat(8, auto);
			grid-row-gap: 22px;

			&__text {
				grid-column-start: 1;
				grid-column-end: 3;
				grid-row: 7;
			}

			&__tags {
				grid-row: 8;
			}
		}
	}
}

@media (max-width: 992px) {}

@media (max-width: 768px) {}

@media (max-width: 576px) {
	.main-container {
		.under-slider-info {
			margin: 10px 0 20px;
		}

		.interiors-detail-info {
			grid-template-columns: 100%;
			grid-column-gap: 0;
			grid-template-rows: repeat(10, auto);
			grid-row-gap: 15px;
			justify-items: center;
			text-align: center;

			h1 {
				grid-column: 1;
				grid-row: 1;
				font-size: 32px;
				line-height: 46px;
			}

			&__status {
				grid-column: 1;
				grid-row: 2;
			}

			&__all-area {
				grid-column: 1;
				grid-row: 3;
			}

			&__type {
				grid-column: 1;
				grid-row: 4;
			}

			&__dev {
				grid-column: 1;
				grid-row: 5;
			}

			&__place {
				grid-column: 1;
				grid-row: 6;
			}

			&__autors {
				grid-column: 1;
				grid-row: 7;
			}

			&__coautors {
				grid-column: 1;
				grid-row: 8;
			}

			&__text {
				grid-column: 1;
				grid-row: 9;

				h2 {
					font-family: 'Manrope',
						sans-serif;
					font-weight: 600;
					font-size: 18px;
					line-height: 25px;
					margin-bottom: 30px;
				}
			}

			&__tags {
				width: 100%;
				height: auto;
				grid-column: 1;
				grid-row: 10;
				border: unset;
				display: flex;
				flex-direction: column;

				&-buttons {
					display: flex;
					flex-direction: column;
					order: 2;

				}

				&-nav {
					display: inline-flex;
					margin: auto;
					order: 1;

					&--text {
						position: relative;
						margin-right: 10px;
					}

					&--arrow {
						position: relative;
						top: 9px;
					}

					&--arrow.active {
						transform: rotate(.5turn);
					}
				}

				&--tag:nth-child(n) {
					width: max-content;
					margin: 0 auto;
					margin-bottom: 10px;
				}
			}
		}
	}
}